<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleContrast"
        />
        <insert-card
            v-if="currentStep === steps.findIndex(step=>step.key === 'insertCardFirst')"
            ref="insertCard"
            :card-type="1"
        />
        <confirm-loading
            v-if="currentStep === steps.findIndex(step=>step.key === 'confirmLoading')"
            ref="readCard"
            :card-type="1"
        />
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
        <select-paper v-if="currentStep === steps.findIndex(step=>step.key === 'selectPaper') && businessTypeCode === 501"/>
        <accept-payment v-if="currentStep === steps.findIndex(step=>step.key === 'acceptPayment') && businessTypeCode === 501"/>
        <pay-qr v-if="currentStep === steps.findIndex(step=>step.key === 'payQr')"/>
        <confirm-loading
            v-if="currentStep === steps.findIndex(step=>step.key === 'confirmLoadingSecond') && businessTypeCode === 501"
            :card-type="2"
        />
        <paper-print v-if="currentStep === steps.findIndex(step=>step.key === 'paperPrint') && businessTypeCode === 501"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete') && businessTypeCode === 501"
            :data="entryAndExitApplyInfo"
            :business-name="entryAndExitReadInfo.personInfo.DocumentType === '25' ? '台湾通行证签注' : '港澳通行证签注'"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import selectPaper from './components/select-paper';
import acceptPayment from './components/accept-payment';
import paperPrint from './components/paper-print';
import complete from '@/components/complete';
import personInfo from './components/person-info';
import confirmLoading from './components/confirm-loading';
import insertCard from './components/insert-card';
import payQr from './components/pay-qr';
import process from '@/components/process';
import { exitEntryFaceRecognition } from '@/service/exitEntry/entryAndExit';
import { wsSend, wsOnMessage } from '@/utils/websocket';
import { cloneDeep } from 'lodash';

export default {
    name: 'ExitEntryHXIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        selectPaper,
        acceptPayment,
        paperPrint,
        complete,
        personInfo,
        confirmLoading,
        insertCard,
        process,
        payQr
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        }),
        ...mapState('permanent', {
            HXWebSocket: 'HXWebSocket',
            lightWebSocket: 'lightWebSocket',
            manufacturerType: 'manufacturerType',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('entryAndExit', {
            entryAndExitReadInfo: 'entryAndExitReadInfo', // todo 离线模式，原来是entryAndExitReadInfo，entryAndExitInfo
            entryAndExitApplyInfo: 'entryAndExitApplyInfo'
        })
    },
    data () {
        return {
            outTime: 0
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', ['setEntryAndExitReadInfo', 'setExitAndEntryId']),
        async handleContrast () {
            const res = await exitEntryFaceRecognition({
                identifiedPhoto: this.binocularCamera,
                manufacturerType: this.manufacturerType
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        wsMessage () {
            wsOnMessage(this.HXWebSocket, (res) => {
                console.log('签注机收到消息', res);
                if (res && res.code === 1) { // 成功
                    this.outTime = 0;
                    if (res.directive === 'In' && this.$refs.insertCard) { // 进卡
                        window.document.onclick();
                        this.$refs.insertCard.insertSuccess();
                    } else if (res.directive === 'Read' && this.$refs.readCard) { // 读卡
                        window.document.onclick();
                        console.log('第一步签注卡读卡信息', res.result);
                        this.setEntryAndExitReadInfo(cloneDeep(res.result)); // 设置签注机读取的信息

                        // this.$refs.readCard.checkHistory(res.result); // todo 离线模式
                        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.exitEntryApply) {
                            this.$refs.readCard.checkRepeatApply(res.result); // 检查是否重复申请签注
                        } else {
                            this.changeStep('down');
                        }
                    } else if (res.directive === 'Vise' || res.directive === 'RePrint') { // 打印
                        wsSend(this.HXWebSocket, {
                            module: 'HangXin',
                            directive: 'Out'
                        });
                        this.changeStep('paperPrint');
                    }
                } else {
                    if (res.directive === 'In' && this.$refs.insertCard) {
                        this.$refs.insertCard.insertFail();
                    } else if (res.directive === 'Read' && this.$refs.readCard) {
                        this.$refs.readCard.readFail();
                    } else if (res.directive === 'Vise') {
                        window.document.onclick();
                        wsSend(this.HXWebSocket, {
                            module: 'HangXin',
                            directive: 'RePrint'
                        });
                    } else if (res.directive === 'RePrint') {
                        this.changeTipPage({ message: '业务办理失败，请至柜台办理！' });
                    } else if (res.directive === 'Out') {
                        if (this.outTime > 3) {
                            this.changeTipPage({
                                message: '退卡失败，请联系管理员取卡！',
                                enableBack: true
                            });
                        } else {
                            wsSend(this.HXWebSocket, {
                                module: 'HangXin',
                                directive: 'Out'
                            });
                            this.outTime++;
                        }
                    }
                }
            });
        }
    },
    mounted () {
        let steps = [];

        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.exitEntryApply) {
            steps = [
                {
                    name: '业务须知',
                    isActive: true,
                    visible: true,
                    key: 'serverInfo'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'faceYitu'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'contrast'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'process'
                },
                {
                    name: '证件识别',
                    isActive: false,
                    visible: true,
                    key: 'insertCardFirst'
                },
                {
                    name: '证件识别',
                    isActive: false,
                    visible: false,
                    key: 'confirmLoading'
                },
                {
                    name: '信息确认',
                    isActive: false,
                    visible: true,
                    key: 'personInfo'
                },
                {
                    name: '签注选择',
                    isActive: false,
                    visible: true,
                    key: 'selectPaper'
                },
                {
                    name: '受理缴费',
                    isActive: false,
                    visible: true,
                    key: 'acceptPayment'
                },
                // {name: '受理缴费', isActive: false, visible: false, key: 'insertCardSecond'},
                {
                    name: '受理缴费',
                    isActive: false,
                    visible: false,
                    key: 'confirmLoadingSecond'
                },
                {
                    name: '签注打印',
                    isActive: false,
                    visible: true,
                    key: 'paperPrint'
                },
                {
                    name: '办理结果',
                    isActive: false,
                    visible: true,
                    key: 'complete'
                }
            ];
        } else {
            steps = [
                {
                    name: '身份识别',
                    isActive: true,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'faceYitu'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'contrast'
                },
                {
                    name: '身份识别',
                    isActive: false,
                    visible: false,
                    key: 'process'
                },
                {
                    name: '证件识别',
                    isActive: false,
                    visible: true,
                    key: 'insertCardFirst'
                },
                {
                    name: '证件识别',
                    isActive: false,
                    visible: false,
                    key: 'confirmLoading'
                },
                {
                    name: '信息确认',
                    isActive: false,
                    visible: true,
                    key: 'personInfo'
                }
            ];
        }
        this.setSteps(steps);

        this.wsMessage();
    },
    beforeDestroy () {
        wsSend(this.lightWebSocket, 'lightCtrl@B3:OFF');
        wsSend(this.HXWebSocket, {
            module: 'HangXin',
            directive: 'Out'
        });
        wsOnMessage(this.HXWebSocket, null);
    }
};
</script>
