<template>
    <div>
        <div class="kd-page-title">
            进行支付
        </div>
        <div class="kd-page-content-wrapper kd-page-padding wrap">
            <div>
                <span>二维码缴费</span>
                <qriously
                    class="payValue"
                    :value="getUrl"
                    :size="300"
                />
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
        <alert-msg
            v-if="alertOn"
            :alertSure="sure"
            :alertCancel="cancel"
            tip-text="确定已支付，并提交业务？"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import alertMsg from '@/components/alert-msg';

export default {
    name: 'TrafficPayQr',
    data () {
        return {
            alertOn: false
        };
    },
    components: { alertMsg },
    computed: {
        ...mapState('entryAndExit', {
            entryAndExitBusinessNum: 'entryAndExitBusinessNum'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        getUrl () {
            return this.CONSTANT.exitEntryPayUrl + this.entryAndExitBusinessNum;
        },
        nextStep () {
            this.alertOn = true;
        },
        sure () {
            this.changeStep('down');
        },
        cancel () {
            this.alertOn = false;
        },
        preStep () { // 上一步
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: space-around;
        align-items: center;

        > div {
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            span {
                text-align: center;
                padding-bottom: 20px;
            }

            .payValue {
                background: #fff;
                margin-bottom: 20px;
            }
        }
    }
</style>
