<template>
    <div>
        <div class="kd-title-content-wrapper wrap-info">
            <div class="baseInfo">
                <el-row>
                    <el-col :span="12">
                        <div class="item">
                            <span class="label">申请人姓名：</span>
                            <span class="">{{ entryAndExitReadInfo.personInfo.ChineseName }}</span>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="item">
                            <span class="label">性别：</span>
                            <span class="">{{ entryAndExitReadInfo.personInfo.Gender }}</span>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <div class="item">
                            <span class="label">通行证号码：</span>
                            <span class="">{{ entryAndExitReadInfo.personInfo.DocumentNo }}</span>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="item">
                            <span class="label">证件有效期：</span>
                            <span class="">{{ entryAndExitReadInfo.personInfo.ExpiryDate }}</span>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <h5>现有签注信息</h5>
            <div class="kd-table-style">
                <el-table
                    :data="entryAndExitReadInfo.list"
                    style="width: 100%"
                >
                    <el-table-column
                        label="签注种类"
                        prop="VisaType"
                    />
                    <el-table-column
                        label="前往地"
                        prop="Destination"
                        show-overflow-tooltip
                    />
                    <el-table-column
                        label="剩余有效次数"
                        prop="MaxEntries"
                        show-overflow-tooltip
                    />
                    <el-table-column
                        label="签注有效期"
                        prop="time"
                        show-overflow-tooltip
                    />
                </el-table>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="this.businessTypeCode === 501"
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
        <alert-msg
            v-if="isRepeatApplyFlag"
            :alertSure="cancel"
            :alertCancel="sure"
            btn-text-left="返回首页"
            btn-text-right="继续"
            tip-text="检测到您之前有未办理完成的签注，是否继续？"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { exitEntryEnableList } from '@/service/exitEntry/entryAndExit';
// import tableConf from '@/components/table-conf';
import alertMsg from '@/components/alert-msg';

export default {
    name: 'ExitEntryHXPersonInfo',
    components: { alertMsg },
    data () {
        return {
            isRepeatApplyFlag: false
        };
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('entryAndExit', {
            entryAndExitInfo: 'entryAndExitInfo',
            exitAndEntryIsRepeat: 'exitAndEntryIsRepeat',
            entryAndExitApplyStatus: 'entryAndExitApplyStatus',
            entryAndExitReadInfo: 'entryAndExitReadInfo' // todo 离线模式，原来是entryAndExitReadInfo，entryAndExitInfo
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', ['setEntryAndExitAvailableList']),
        validityPeriod (time) {
            console.log('证件有效期', time);
            return time ? (time.substring(0, 4) + '/' + time.substring(4, 6) + '/' + time.substring(6, 8)) : '';
        },
        nextStep () {
            this.isRepeatApplyFlag = this.exitAndEntryIsRepeat;
            // 若无重复申请，则直接跳转下一步
            if (!this.isRepeatApplyFlag) {
                this.normalStep();
            }
        },
        // 继续办理
        sure () {
            console.log('上次办理业务状态', this.entryAndExitApplyStatus);
            if (this.entryAndExitApplyStatus === '0') {
                this.changeStep('acceptPayment');
            } else if (this.entryAndExitApplyStatus === '1') {
                this.changeStep('confirmLoadingSecond');
            } else {
                this.changeStep('down');
            }
        },
        // 暂不能重新办理,只能退出
        cancel () {
            this.changeStep('back');
        },
        async normalStep () {
            this.alertOn = false;
            const params = {
                // businessType: this.entryAndExitInfo.zjzl === '21' ? 0 : 1, // 业务类型 0港澳通行证签注;1台湾通行证 (需要知道证件类型)
                // validityPeriod: this.validityPeriod(this.entryAndExitInfo.zjyxqz)// 证件有效期 格式：'2030/11/11'
                businessType: this.entryAndExitReadInfo.personInfo.DocumentType === '21' ? 0 : 1, // 业务类型 0港澳通行证签注;1台湾通行证 (需要知道证件类型)
                validityPeriod: this.validityPeriod(this.entryAndExitReadInfo.personInfo.ExpiryDate)// 证件有效期 格式：'2030/11/11'
            };
            const res = await exitEntryEnableList(params);
            if (res && res.code === '0') { // 返回数据成功
                const allList = [];
                Object.keys(res.result).forEach((key) => {
                    if (res.result[key] && res.result[key].length > 0) {
                        const data = {};
                        data.destination = res.result[key][0].destination;
                        data.lists = res.result[key];
                        const typeArr = [];
                        data.lists.forEach((i) => {
                            i.isActive = false;
                            typeArr.push(i.endorsementType);
                        });
                        data.typeArr = [...new Set(typeArr)]; // 存放签证类型 个人旅游、团体旅游
                        allList.push(data);
                    }
                });
                this.setEntryAndExitAvailableList(allList);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding: 4vh 5vh;

        .baseInfo {
            box-sizing: border-box;
            width: 74.37vw;
            font-size: 2.4vh;
            margin: 0 auto;
            //color:rgba(255,255,255,1);
            .item {
                //width: 36vw;
                height: 3.6vh;
                //text-align: right;
                line-height: 3.6vh;
                margin-bottom: 3.0vh;
                font-weight: 600;

                .label {
                    display: inline-block;
                    width: 14vw;
                    font-weight: 400;
                    color: #e0ebf796;
                    text-align: right;
                }
            }
        }

        h5 {
            color: #e0ebf796;
            font-size: 2.4vh;
            margin: 1vh 0;
        }
    }

</style>
