<template>
    <div>
        <div
            class="kd-page-title"
            :class="againCheck === true ? 'red': ''"
        >
            {{ text }}
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <img
                class="moveCard"
                :src="imgUrl"
            >
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="againCheck"
                icon="kedaIcon-policework"
                text="确认"
                :button-handle="ensureInsert"
            />
        </div>
    </div>
</template>

<script>
import exitMove from '@/assets/images/exitMove.gif';
import moneyMove from '@/assets/images/moneyMove.gif';
import { mapState, mapMutations } from 'vuex';
import { wsSend, wsOnMessage } from '@/utils/websocket';

export default {
    name: 'ExitEntryHXInsertCard',
    props: { cardType: Number },
    data () {
        return {
            text: '',
            imgUrl: '',
            againCheck: false
        };
    },
    computed: {
        ...mapState('permanent', {
            bankWebSocket: 'bankWebSocket',
            HXWebSocket: 'HXWebSocket',
            lightWebSocket: 'lightWebSocket'
        }),
        ...mapState('entryAndExit', {
            entryAndExitSumFee: 'entryAndExitSumFee',
            entryAndExitBusinessNum: 'entryAndExitBusinessNum'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', ['setEntryAndExitPayResult']),
        handleMoney () {
            wsOnMessage(this.bankWebSocket, (res) => {
                console.log('银行硬件收到消息', res);
                if (res && res.code === 1) {
                    this.setEntryAndExitPayResult(res.result);
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            });
            wsSend(this.bankWebSocket, {
                module: 'BankPos',
                directive: 'Pay',
                obj: {
                    money: this.entryAndExitSumFee * 100,
                    businessCode: this.entryAndExitBusinessNum
                }
            });
        },
        insertSuccess () {
            this.changeStep('down');
        },
        insertFail () {
            this.text = '请先插入通行证，再点击确认';
            this.againCheck = true;
            // this.changeTipPage({message: '进卡失败'});
        },
        ensureInsert () {
            wsSend(this.HXWebSocket, {
                module: 'HangXin',
                directive: 'In'
            });
        },
        payFail () {
            this.changeTipPage({ message: '支付失败，请退出重试！' });
        }
    },
    mounted () {
        if (this.cardType === 1) {
            this.text = '请按以下图示插入您的港澳通行证';
            this.imgUrl = exitMove;
            wsSend(this.lightWebSocket, 'lightCtrl@B3:ON');
            setTimeout(() => {
                wsSend(this.HXWebSocket, {
                    module: 'HangXin',
                    directive: 'In'
                });
            }, 3000);
        } else if (this.cardType === 2) {
            this.text = '请按以下图示插入您的银行卡';
            this.imgUrl = moneyMove;
            this.handleMoney();
        }
    },
    beforeDestroy () {
        wsOnMessage(this.bankWebSocket, null);
    }
};
</script>

<style lang="scss" scoped>
    .red {
        color: red;
    }

    .wrap-info {
        padding: 8.5vh 0;
        position: relative;

        .entryBox {
            width: 43.15vh;
            height: 9.15vh;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 0.5vh;
            border: 1vh solid #F2F9FF;/*no*/
            background: #fff;

            > div {
                width: 35.15vh;
                height: 1.3vh;
                background: rgba(153, 153, 153, 1);
                box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.5);
                border-radius: 0.5vh;
            }
        }

        .moveCard {
            position: absolute;
            z-index: 1;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }

        #idCard {
            transform: rotateZ(-90deg);
            width: 37.15vh;
            height: 24.15vh;
            border-radius: 0.5vh;
            background: url("../../../../assets/images/cardno_bg.png");
            background-size: 100% 100%;
            margin: 0 auto;
        }

    }

</style>
