<template>
    <div>
        <div class="kd-page-title">
            请认真核对打印信息是否一致
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <div
                class="paper-item"
                v-for="(item, index) in entryAndExitSelectList"
                :key="index"
            >
                <div class="item-info">
                    <p>
                        <span class="label">种&nbsp;&nbsp;&nbsp;类：</span>
                        <span>{{ item.endorsementType === 0 ? '个人旅游' : '团体旅游' }}</span>
                        <span>{{ item.lastTimes === 1 ? '壹' : '贰' }}次往返有效</span>
                    </p>
                    <p>
                        <span class="label">有效期：</span>
                        <span>{{ $momentFormat(item.begDate) }} - {{ $momentFormat(item.endDate) }}</span>
                    </p>
                    <p>
                        <span class="label">备&nbsp;&nbsp;&nbsp;注：</span>
                        <span v-if="item.destination !== '台湾'">持证人每次在{{ item.destination }}逗留不超过7天</span>
                        <span v-if="item.destination === '台湾'">本签注签发给{{ entryAndExitReadInfo.personInfo.DocumentNo }}号通行证的持证人</span>
                    </p>
                </div>

                <!--<div class="item-img">-->
                <!--<qriously class="payValue" value="entryAndExitReadInfo.DocumentNo" :size="170"/>-->
                <!--</div>-->
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="退出"
                type-class="home"
            />
            <kd-button
                text="确定"
                :button-handle="handingOverVise"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { exitEntryResult } from '@/service/exitEntry/entryAndExit';

export default {
    name: 'ExitEntryHXPaperPrint',
    computed: {
        ...mapState('entryAndExit', {
            entryAndExitReadInfo: 'entryAndExitReadInfo', // todo 离线模式，原来是entryAndExitReadInfo，entryAndExitInfo
            entryAndExitSelectList: 'entryAndExitSelectList',
            entryAndExitBusinessNum: 'entryAndExitBusinessNum'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', ['setEntryAndExitApplyInfo']),
        async handingOverVise () {
            const res = await exitEntryResult({
                ywbh: this.entryAndExitBusinessNum,
                flag: 1
            });
            if (res && res.code === '0') {
                this.setEntryAndExitApplyInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }

    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 4vh 5vh;

        div.paper-item {
            width: 74.37vw;
            display: flex;
            justify-content: space-between;

            div.item-info {
                p {
                    font-weight: 600;
                    color: rgba(255, 255, 255, 1);
                    line-height: 4.3vh;
                    font-size: 2.2vh;

                    span.label {
                        font-weight: 400;
                    }
                }
            }

            div.item-img {
                display: flex;
                justify-content: center;
                align-items: center;

                img {

                }
            }
        }

    }

    .payValue {
        background: #fff;
    }
</style>
