<template>
    <div class="acceptPayment">
        <div class="kd-page-title">
            申请信息确认
        </div>
        <div class="kd-page-content-wrapper kd-table-style">
            <el-table
                ref="multipleTable"
                :data="entryAndExitSelectList"
                :highlight-current-row="true"
                style="width: 100%"
            >
                <el-table-column
                    label="签注种类"
                >
                    <template slot-scope="scope">
                        <div style="text-indent: 20px;">
                            {{ scope.row.endorsementType === 0 ? '个人旅游' : '团体旅游' }}
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                    label="前往地"
                    prop="destination"
                />
                <el-table-column
                    label="当前签注次数"
                    prop="lastTimes"
                />
                <el-table-column
                    label="签注有效期"
                    prop="date"
                    width="500"
                >
                    <template slot-scope="scope">
                        <div>{{ $momentFormat(scope.row.begDate) }}至{{ $momentFormat(scope.row.endDate) }}</div>
                    </template>
                </el-table-column>
            </el-table>
            <div class="kd-total-content">
                <div class="left">
                    注意：重新签注后，原有签注信息此处将作废
                </div>
                <div class="right">
                    <p>总费用：￥<span>{{ entryAndExitSumFee }}</span>元</p>
                </div>
            </div>
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="立即支付"
                :button-handle="savePay"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
// import ConfirmLoading from './confirm-loading';
// import InsertCard from './insert-card';
// import CardPassword from './card-password';

export default {
    name: 'ExitEntryHXAcceptPayment',
    computed: {
        ...mapState('entryAndExit', {
            entryAndExitSumFee: 'entryAndExitSumFee',
            entryAndExitSelectList: 'entryAndExitSelectList'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        savePay () { // 立即支付
            this.changeStep('down');
        }
    }

};
</script>
<style lang="scss" scoped>
    .acceptPayment ::v-deep .left {
        color: red;
        font-size: 16px;
    }
</style>
