<template>
    <div>
        <div class="select-box">
            <div
                v-for="(item, index) in availableLists"
                :key="index"
            >
                <!-- 签注城市 类型 -->
                <div class="select-title">
                    <h6>{{ item.destination }}签注</h6>
                </div>
                <!-- 签注详情 -->
                <div class="content">
                    <div
                        v-for="(type, i) in item.typeArr"
                        :key="i"
                    >
                        <div
                            class="item"
                            v-if="checkExit(item.lists, type)"
                        >
                            <div class="left">
                                {{ type === 0 ? '个人旅游' : '团体旅游' }}
                            </div>
                            <div class="right">
                                <template v-for="(detailItem, index2) in item.lists">
                   <span
                       v-if="detailItem.endorsementType === type"
                       :key="index2"
                       class="detail-item"
                       :class="{'item-active':detailItem.isActive}"
                       @click="itemChange(detailItem, item.lists)"
                   >
                  {{ detailItem.title }}
                </span>
                                </template>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="提交"
                :button-handle="nextStep"
                :but-disable="!(chooseItem.length > 0)"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { exitEntrySelect } from '@/service/exitEntry/entryAndExit';

export default {
    name: 'ExitEntryHXSelectPaper',
    data () {
        return {
            chooseItem: []
        };
    },
    computed: {
        ...mapState('entryAndExit', {
            availableLists: 'entryAndExitAvailableList',
            entryAndExitReadInfo: 'entryAndExitReadInfo' // todo 离线模式，原来是entryAndExitReadInfo，entryAndExitInfo
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', ['setEntryAndExitSumFee', 'setEntryAndExitBusinessNum', 'setEntryAndExitSelectList']),
        checkExit (list, type) {
            if (list && list.length > 0) {
                return list.some((obj, index) => {
                    return obj.endorsementType === type;
                });
            }
        },
        validityPeriod (time) {
            return time ? (time.substring(0, 4) + '/' + time.substring(4, 6) + '/' + time.substring(6, 8)) : '';
        },
        itemChange (item, lists) { // 签注详情变化
            this.chooseItem = [];
            lists.forEach((i) => {
                i.isActive = i.id === item.id;
            });
            this.availableLists.forEach((i) => {
                const info = i.lists.find((j) => {
                    return j.isActive === true;
                });
                if (info) {
                    this.chooseItem.push(info);
                }
            });
        },
        async nextStep () { // 下一步
            if (this.chooseItem.length <= 0) {
                return;
            }
            const endorseIds = [];
            this.chooseItem.forEach((item) => {
                endorseIds.push(item.id);
            });

            const res = await exitEntrySelect({
                endorseIds: endorseIds,
                idNum: this.entryAndExitReadInfo.personInfo.IDNo
            });
            if (res && res.code === '0') {
                if (res.result.status === 3) {
                    this.changeTipPage({ message: '请至柜台办理' });
                }
                this.setEntryAndExitSumFee(res.result.sumFee);
                this.setEntryAndExitBusinessNum(res.result.qzYwbh);
                this.setEntryAndExitSelectList(res.result.userEndorseList);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        preStep () { // 上一步
            this.changeStep('up');
        }

    }

};
</script>

<style lang="scss" scoped>
    .inline-block {
        display: inline-block;
    }

    .select-box {
        margin: 7vh auto 0;
        height: 56.5vh;
        width: 1200px;
        color: #fff;

        & > div {
            margin-bottom: 30px;

            &:first-of-type {
                margin-bottom: 50px;
            }
        }

        .select-title {
            height: 40px;
            line-height: 40px;
            padding-left: 8px;

            h6 {
                margin: auto;
                font-size: 32px;
                font-weight: normal;
                text-align: center;
                margin-bottom: 25px;
            }
        }

        .content {
            .item {
                margin-top: 20px;
                display: flex;
                flex-direction: row;

                .left {
                    width: 60px;
                    padding: 12px 0 0;
                    font-size: 24px;
                    line-height: 30px;
                }
            }

        }

        .address-name {
            display: inline-block;
            margin-right: 30px;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
        }

        span.detail-item {
            display: inline-block;
            margin: 8px;
            width: 264px;
            height: 72px;
            font-size: 24px;
            color: rgba(255, 255, 255, 1);
            line-height: 72px;
            text-align: center;
            background: linear-gradient(180deg, rgba(112, 163, 216, 1) 0%, rgba(58, 132, 191, 1) 100%);
            border-radius: 8px;
            border: 1PX solid;/*no*/
            cursor: pointer;

            &.item-active {
                width: 264px;
                height: 72px;
                background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
                border-radius: 8px;
                border: 1PX solid;/*no*/
                border-image: none;
            }
        }

        span.disabled {
            background: linear-gradient(180deg, #c5d7ea 0%, #a5bfd8 100%);
        }
    }

    //覆盖element ui样式
    ::v-deep .el-radio__label {
        font-size: 24px;
        padding-left: 10px;
        color: white;
    }

    ::v-deep .el-checkbox__label {
        font-size: 24px;
    }

    ::v-deep .el-radio__inner {
        width: 18px;
        height: 18px;
    }

    ::v-deep .el-radio__inner::after {
        width: 8px;
        height: 8px;
    }

    ::v-deep .el-radio__input {
        margin-bottom: 4px;
    }

    ::v-deep .el-radio__input.is-checked + .el-radio__label {
        color: white;
    }

</style>
