<template>
    <div class="confirm-loading">
        <div class="loading-content">
            <img
                class="loading-type"
                :src="imgUrl"
            >
            <img
                class="loading-progress"
                src="../../../../assets/images/progress.gif"
            >
            <p>{{ text }}</p>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsSend } from '@/utils/websocket';
import waitCard from '@/assets/images/wait_card.png';
import waitUp from '@/assets/images/wait_up.png';
import waitPrint from '@/assets/images/wait_print.png';
import { exitEntryCheckPrePay, checkRepeatApply, exitEntryApplyInfo, exitEntryCheckHistory } from '@/service/exitEntry/entryAndExit';

export default {
    name: 'ExitEntryHXConfirmLoading',
    props: { cardType: Number },
    data () {
        return {
            text: '',
            imgUrl: ''
        };
    },
    computed: {
        ...mapState('permanent', {
            HXWebSocket: 'HXWebSocket',
            manufacturerType: 'manufacturerType'
        }),
        ...mapState('common', {
            identifyCard: 'identifyCard'
        }),
        ...mapState('entryAndExit', {
            exitAndEntryIsRepeat: 'exitAndEntryIsRepeat',
            entryAndExitBusinessNum: 'entryAndExitBusinessNum',
            entryAndExitApplyStatus: 'entryAndExitApplyStatus',
            entryAndExitReadInfo: 'entryAndExitReadInfo' // todo 离线模式，原来是entryAndExitReadInfo，entryAndExitInfo
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('entryAndExit', [
            'setEntryAndExitInfo', 'setExitAndEntryIsRepeat', 'setExitAndEntryId',
            'setEntryAndExitBusinessNum', 'setEntryAndExitSelectList', 'setEntryAndExitSumFee',
            'setEntryAndExitApplyStatus'
        ]),

        async checkHistory (data) {
            const res = await exitEntryCheckHistory({
                zjzl: data.personInfo.DocumentType,
                zjhm: data.personInfo.DocumentNo
            });
            if (res && res.code === '0') {
                this.setEntryAndExitInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleExitCard () {
            this.text = '正在识别，请耐心等待';
            this.imgUrl = waitCard;
            wsSend(this.HXWebSocket, {
                module: 'HangXin',
                directive: 'Read'
            });
        },
        async checkRepeatApply (data) {
            // 出入境查询是否重复申请
            const res = await checkRepeatApply({
                zjzl: data.personInfo.DocumentType,
                zjhm: data.personInfo.DocumentNo
            });
            if (res && res.code === '0') {
                if (res.result.checkStatus === 2) { // 未重复申请
                    this.setExitAndEntryIsRepeat(false);
                    this.changeStep('down');
                } else if (res.result.checkStatus === 1) { // 重复申请，需查询有无支付成功
                    this.setExitAndEntryIsRepeat(true);

                    this.setEntryAndExitBusinessNum(res.result.qzYwbh);
                    this.setEntryAndExitSelectList(res.result.list);
                    this.setEntryAndExitSumFee(res.result.sumFee);

                    this.checkPayStatus();
                } else if (res.result.checkStatus === -1) { // 重复申请并且不能在我们机器上办理的
                    this.changeTipPage({ message: '存在重复的申请，请至柜台办理！' });
                } else {
                    this.changeTipPage({ message: res.result.msg });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        handleMoney () {
            this.text = '正在查询支付情况，请耐心等待';
            this.imgUrl = waitUp;
            this.checkPayStatus();
        },
        async checkPayStatus () {
            const res = await exitEntryCheckPrePay({
                ywbh: this.entryAndExitBusinessNum,
                idNum: this.entryAndExitReadInfo.personInfo.IDNo
            });
            if (res && res.code === '0') {
                if (res.result === 2) {
                    this.setEntryAndExitApplyStatus('1');
                    this.handleEndorsement();
                } else { // 未支付
                    this.setEntryAndExitApplyStatus('0');
                    this.changeStep('payQr');
                }
            } else if (res) {
                this.changeTipPage({ message: '查询失败，请到柜台办理！' });
            }
        },
        async handleEndorsement () {
            window.document.onclick();
            this.text = '正在为您签注打印，请耐心等待';
            this.imgUrl = waitPrint;
            this.confirmPrint();
        },

        // 确认打印
        async confirmPrint () {
            const res = await exitEntryApplyInfo({});
            console.log('制签证接口返回', res);
            wsSend(this.HXWebSocket, {
                module: 'HangXin',
                directive: 'Vise'
            });
        },
        readFail () {
            this.changeTipPage({ message: '未能读取到卡内信息，请退出重试' });
        }
    },
    async mounted () {
        if (this.cardType === 1) { // 签注卡
            this.handleExitCard();
        } else if (this.cardType === 2) { // 银行卡
            if (this.entryAndExitApplyStatus === '1') {
                this.handleEndorsement();
            } else {
                this.handleMoney();
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .confirm-loading {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 999;
        background-color: rgba(0, 0, 0, 0.7);

        .loading-content {
            width: 400px;
            text-align: center;

            img.loading-type {
                width: 200px;
            }

            img.loading-progress {
                width: 100%;
            }

            > p {
                text-align: center;
                font-size: 26px;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
                line-height: 36px;

            }

        }
    }

</style>
